* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.App {
    padding-top: 50px;
}

#header {
    display: flex;
    align-content: center;
    justify-content: space-between;
    margin-bottom: 10px;
    width: 80vw;
}

#search-bar {
    flex: 0.9;
}

.add-color {
    align-self: center;
}

#search-form input[type="text"] {
    width: 100%;
    height: 40px;
    margin: 5px;
    padding: 3px 7px;
    font-size: 17px;
    font-family: inherit;
}

.btn {
    display: inline-block;
    cursor: pointer;
    text-decoration: none;
    font-family: inherit;
}

.btn:focus {
    outline: none;
}

.btn:active {
    transform: scale(0.98);
}

.btn-block {
    display: block;
    width: 100%;
}

.colors {
    display: flex;
    align-content: center;
    justify-content: start;
    flex-wrap: wrap;
    width: 80vw;
}

.color {
    width: 97px;
    height: 97px;
    margin: 10px 10px;
    border: 1px solid;
    border-radius: 5px;
}
